import React, { useState } from 'react';
import './Navbar.scss';

import { Link, NavLink } from 'react-router-dom';

export const Navbar = () => {
  const [search, setSearch] = useState(true);
  const [events, setEvents] = useState(true);
  const [standings, setStanding] = useState(true);
  const [community, setCommunity] = useState(true);

  const showSearchbar = () => {
    setSearch(!search);
  };

  return (
    <>
      <div className="nav-container">
        {/* top nav */}
        <section className="top-navbar">
          <section className="logo-section">
            <Link to="/">Highnoon Gaming</Link>
          </section>
        </section>

        {/* bottom nav */}
        <section className="bottom-navbar">
          <ul className="ul-links">
            {/* home */}
            <NavLink
              exact
              to="/"
              className="mainLink"
              activeClassName="activeLink"
            >
              <li>Home</li>
            </NavLink>

            {/* events */}
            <NavLink
              exact
              to="/events"
              className="mainLink"
              activeClassName="activeLink"
              onMouseEnter={() => setEvents(!events)}
              onMouseLeave={() => setEvents(true)}
            >
              <li>
                Events
                <i
                  className={
                    events
                      ? 'fas fa-angle-double-up customPos-arrow'
                      : 'fas fa-angle-double-down customPos-arrow'
                  }
                ></i>
                <ul className={events ? 'dropdown-list' : 'show-dropdown-list'}>
                  <section className="li-bg">
                    <li className="dropdown-content">Weekly Events</li>
                    <li className="dropdown-content">Special Events</li>
                    <li className="dropdown-content">Tournaments</li>
                    <li className="dropdown-content">Animekon</li>
                  </section>
                </ul>
              </li>
            </NavLink>

            {/* standings */}
            <NavLink
              exact
              to="/standings"
              className="mainLink"
              activeClassName="activeLink"
              onMouseEnter={() => setStanding(!standings)}
              onMouseLeave={() => setStanding(true)}
            >
              <li>
                Standings{' '}
                <i
                  className={
                    standings
                      ? 'fas fa-angle-double-up'
                      : 'fas fa-angle-double-down'
                  }
                ></i>
                <ul
                  className={
                    standings ? 'dropdown-list' : 'show-dropdown-list standings'
                  }
                >
                  <section className="li-bg">
                    <li className="dropdown-content">Street Fighter</li>
                    <li className="dropdown-content">Tekken</li>
                    <li className="dropdown-content">Dragonball Fighter Z</li>
                    <li className="dropdown-content">Super Smash Bros.</li>
                    <li className="dropdown-content">Mortal Kombat</li>
                  </section>
                </ul>
              </li>
            </NavLink>

            {/* community */}
            <NavLink
              exact
              to="/community"
              className="mainLink"
              activeClassName="activeLink"
              onMouseEnter={() => setCommunity(!community)}
              onMouseLeave={() => setCommunity(true)}
            >
              <li>
                Community
                <i
                  className={
                    community
                      ? 'fas fa-angle-double-up customPos-arrow'
                      : 'fas fa-angle-double-down customPos-arrow'
                  }
                ></i>
                <ul
                  className={
                    community ? 'dropdown-list' : 'show-dropdown-list standings'
                  }
                >
                  <section className="li-bg">
                    <li className="dropdown-content">Discord</li>
                    <li className="dropdown-content">Twitch</li>
                    <li className="dropdown-content">Youtube</li>
                    <li className="dropdown-content">Twitter</li>
                    <li className="dropdown-content">
                      <a
                        onClick={
                          'http://www.instagram.com/highnoongaming/?hl=en'
                        }
                        href="http://www.instagram.com/highnoongaming/?hl=en"
                        target="_blank"
                        rel="noopener noreferrer"
                      >
                        Instagram
                      </a>
                    </li>
                    <li className="dropdown-content">Facebook</li>
                  </section>
                </ul>
              </li>
            </NavLink>

            <NavLink
              exact
              to="/news"
              className="mainLink"
              activeClassName="activeLink"
            >
              <li>News</li>
            </NavLink>
          </ul>

          <section className="account-search">
            <Link to="/login">
              <p className="login">Login</p>
            </Link>
            <p className="slash">/</p>
            <Link to="/signup">
              <p className="signup">Signup</p>
            </Link>
            <i className="fas fa-search" onClick={showSearchbar} />
          </section>
        </section>
      </div>

      <form action="" method="get">
        <input type="search" className={search ? 'search' : 'showSearch'} />
      </form>
    </>
  );
};

export default Navbar;
